.aiwave-separator{
    &.has-position-bottom{
        position: absolute;
        width: 100%;
        height: auto;
        bottom: -2px;
        left: 0;
        right: 0;
        @media #{$sm-layout} {
            bottom: -5px;
        }
    }
    &.has-position-top{
        position: absolute;
        width: 100%;
        height: auto;
        left: 0;
        right: 0;
        bottom: 100%;
        top: auto;
    }
}
.separator-animated{
    position: relative;
    overflow: hidden;
    height: 2px;
    width: 100%;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(255, 255, 255, 0) 0%, rgba(111, 100, 233, 0.60) 50%, rgba(0, 0, 0, 0) 100%);
    &.animated-true{
        &::before{
            content: '';
            position: absolute;
            width: 3%;
            height: 100%;
            background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%,
                                                rgba(255, 255, 255, 0) 0%,
                                                var(--color-primary) 50%, 
                                                rgba(0, 0, 0, 0) 100%);
            backdrop-filter: blur(20px);
            animation: separateline 5s ease-in-out infinite;
        }
    }
}
@keyframes separateline
{
    0%{
        left: 0;
    }
    100%{
        left: 110%;
    }
}

.bg-blur-filter {
    position: absolute;
    left: 0;
    top: 50%;
    z-index: -1;
    width: 100%;
    height: 500px;
    transform: translate(-50%,-50%);
    background-image:linear-gradient(
        45deg
        , var(--color-primary), var(--color-primary));
    opacity: 0.25;
    -webkit-filter: blur(100px);
    filter: blur(100px);
}

.theme-shape {
    position: relative;
    z-index: 2;
    overflow: hidden;

    &::before {
        position: absolute;
        left: -250px;
        top: 250px;
        right: auto;
        bottom: auto;
        z-index: -1;
        width: 500px;
        height: 500px;
        border-radius: 1000px;
        background-image: linear-gradient(45deg, var(--color-primary), var(--color-primary));
        opacity: 0.2;
        filter: blur(100px);
        content: "";
    }

    &::after {
        position: absolute;
        z-index: -1;
        width: 500px;
        height: 500px;
        border-radius: 1000px;
        opacity: 0.2;
        filter: blur(100px);
        content: "";
        left: auto;
        top: -250px;
        right: -250px;
        bottom: auto;
        background-image: linear-gradient(45deg, var(--color-primary), var(--color-tertiary));
        
    }
}



.gradient-btn-shadow{
    box-shadow: inset 0 0 60px whitesmoke,
                    inset 20px 0 80px #f0f,
                    inset -20px 0 80px #0ff,
                    inset 20px 0 300px #f0f,
                    inset -20px 0 300px #0ff,
                            0 0 50px #fff,
                            -10px 0 80px #f0f,
