.chatai-sec{
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    background-color: var(--color-blackest);
    gap: 0 5px;
    .box{
        position: relative;
        font-weight: var(--p-bold);
        text-transform: uppercase;
        font-size: var(--font-size-b3);
        color: var(--color-dark);
        &:nth-child(odd){
            &:before{
                content: ' AIWave ';
            }
        }
        &:nth-child(even){
            &:before{
                content: 'Text transform';
                filter: hue-rotate(180deg);
            }
        }
        &.coloranimate{
            color: rgba(172, 172, 172, 0.2);
            text-shadow: 0 0 5px rgba(172, 172, 172, 0.2),
                        0 0 20px rgba(172, 172, 172, 0.2),
                        0 0 50px rgba(172, 172, 172, 0.2),;
        }
    }
    .frame-image{
        position: absolute;
        z-index: 10;
        width: 50%;
        height: auto;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) !important;
    }
}

.color-off{
    opacity: 0.5;
}

.top-flashlight{
    position: relative;
    &::before{
        content: "";
        width: 150px;
        height: 150px;
        background: var(--color-primary);
        position: absolute;
        left: 50%;
        top: -60px;
        transform: translateY(-50%) translateX(-50%);
        border-radius: 100%;
        filter: blur(70px);
    }
    &.leftside{
        &::before{
            left: 0;
            top: 0;
        }
    }
    &.light-xl{
        &::before{
            width: 300px;
            filter: blur(140px);
        }
    }
}
.bottom-flashlight{
    position: relative;
    &::before{
        content: "";
        width: 50px;
        height: 50px;
        background: var(--color-primary);
        position: absolute;
        left: 50%;
        bottom: -50%;
        transform: translateY(-50%) translateX(-50%);
        filter: blur(20px);
    }
}
// Border flashlight
.border-gradient{
    position: relative;
    overflow: hidden;
    &::before{
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: linear-gradient(to right, var(--color-primary-gradient-start), var(--color-primary-gradient-end));
        width: 200%;
        height: 200%;
        transition:  0.5s,top 0s,left 0s;
        border-radius: var(--radius);
    }
    &::after{
        content: '';
        position: absolute;
        inset: 2px;
        background: var(--color-blackest);
        border-radius: var(--radius);
    }
    & > *{
        z-index: 2;
        position: relative;
    }
    &.bg-secondary{
        &::before{
            background: radial-gradient(var(--color-primary), transparent, 
            transparent);
        }
    }
    &.bg-tertiary{
        &::before{
            background: radial-gradient(var(--color-tertiary), transparent, 
            transparent);
        }
    }
}






// Pre Loader CSS
.preloader {
    background: var(--color-dark);
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 9999999;
    top: 0;
    left: 0;
  }
  @keyframes bgg {
    0% { background: var(--color-primary-light); }
    50% { background: var(--color-primary); }
    100% { background: var(--color-primary); }
  }
  .loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 70px;
    height: 70px;
    margin: auto;
  }
  .loader .circle {
    position: absolute;
    width: 50px;
    height: 50px;
    opacity: 0;
    transform: rotate(225deg);
    animation-iteration-count: infinite;
    animation-name: orbit;
    animation-duration: 5.5s;
    left: 3px;
    top: -5px;
  }
  .loader .circle:after {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 100%;
    animation: bgg 1500ms ease-in alternate infinite;
  }
  .loader .circle:nth-child(2) {
    animation-delay: 240ms;
  }
  .loader .circle:nth-child(3) {
    animation-delay: 480ms;
  }
  .loader .circle:nth-child(4) {
    animation-delay: 720ms;
  }
  .loader .circle:nth-child(5) {
    animation-delay: 960ms;
  }
  @keyframes orbit {
    0% {
      transform: rotate(225deg);
      opacity: 1;
      animation-timing-function: ease-out;
    }
    7% {
      transform: rotate(345deg);
      animation-timing-function: linear;
    }
    30% {
      transform: rotate(455deg);
      animation-timing-function: ease-in-out;
    }
    39% {
      transform: rotate(690deg);
      animation-timing-function: linear;
    }
    70% {
      transform: rotate(815deg);
      opacity: 1;
      animation-timing-function: ease-out;
    }
    75% {
      transform: rotate(945deg);
      animation-timing-function: ease-out;
    }
    76% {
      transform: rotate(945deg);
      opacity: 0;
    }
    100% {
      transform: rotate(945deg);
      opacity: 0;
    }
  }

  .rating-title{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
  }
  .rating{
    display: flex;
